<template>
  <!-- 采购申请新增修改 -->
  <div class='jybdOrderDetail'>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>采购管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">采购订单列表</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>采购订单详情</el-breadcrumb-item>
    </el-breadcrumb>

    <div class="paymentInfo">
      <p><img :src="params.extend_order_common.pay_pic" alt="图片加载失败"></p>
      <p>付款账户名：{{ params.extend_order_common.pay_account }} </p>
      <p>付款金额： {{ params.extend_order_common.pay_amount }}</p>
      <p v-if='params.extend_order_common.reciver_info'>账户预留手机号： {{ params.extend_order_common.pay_phone }}</p>
    </div>

    <div class="stepBar">
      <el-steps :active="computedStatus" align-center>
        <el-step title="提交订单" :description="$publicFution.getAllTime(params.add_time * 1000)"></el-step>
        <el-step title="支付订单" :description="$publicFution.getAllTime(params.payment_time * 1000)"></el-step>
        <el-step title="商家发货" :description="$publicFution.getAllTime(params.extend_order_common.shipping_time * 1000)"></el-step>
        <el-step title="确认收货" :description="$publicFution.getAllTime(params.finnshed_time * 1000)"></el-step>
      </el-steps>
    </div>

    <div class="table_container">
      <xTable ref='xTable' :tableColumn='tableColumn' :xData="params.extend_order_goods" />
    </div>

    <div class='total'>
      <p>运费：￥ {{ params.shipping_fee }}</p>
      <p>订单金额：￥ <span class='mainRed'>{{ params.order_amount }}</span> 元</p>
    </div>

    <div class="dialog-footer">
      <el-button @click="goBack">返回</el-button>
    </div>
  </div>
</template>

<script>
import { tableHead } from "./js/orderSee";
export default {
  name: "inBusinessPurchaseOrderListPurchaseOrderAdd",
  mixins: [tableHead],
  data() {
    return {
      params: {
        extend_order_common: {}
      }
    };
  },
  computed: {
    computedStatus: function() {
      if (this.params.finnshed_time > 0) {
        return 4;
      }
      if (this.params.extend_order_common.shipping_time > 0) {
        return 3;
      }
      if (this.params.payment_time > 0) {
        return 2;
      }
      if (this.params.add_time > 0) {
        return 1;
      }
      return 0;
    }
  },
  methods: {
    sumbit() {
      this.$refs.params.validate(valid => {
        if (valid) {
          var tableBox = this.$refs.xTable
            ._proxy("getTableData")
            .fullData.concat([]);
          this.$refs.xTable._proxy("validate", tableBox).then(errMap => {});
        }
      });
    },
    goBack() {
      this.$router.push("/purchase/order");
    }
  },
  created() {
    if (this.$route.query.id) {
      this.$axMethod
        .getServiceOrderInfo({ ec_order_sn: this.$route.query.id })
        .then(({ data }) => {
          if (data.code == 200) {
            this.params = data.data.order;
          }
        });
    }
  }
};
</script>

<style scoped lang='less'>
.jybdOrderDetail {
  .stepBar {
    width: 60%;
    text-align: left;
    margin: 30px 0px;
  }
  .paymentInfo {
    border-bottom: solid 1px #eaeaea;
    text-align: center;
    p {
      margin: 10px 0px;
    }
    img {
      width: 300px;
    }
  }
  .total {
    text-align: right;
    p {
      margin-top: 10px;
    }
  }
}
</style>